@import url(https://fonts.googleapis.com/css2?family=PT+Sans&family=Ubuntu:ital@1&display=swap);
.chat {
  width: 65rem;
  height: 48rem;
  /* background: #0000ff; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-bottom: 2px solid var(--color-1);
  padding: 1rem;
}

.chat__label {
  width: 100%;
  height: 4rem;
  display: flex;
  justify-content: center;
}

.chat__label--input {
  font-family: sans-serif;
  width: 80%;
  flex: 5 1;
  font-size: 1.9rem;
  padding: 0.5rem;
  color: #fff;
  background: #333;
  border: none;
  justify-self: flex-end;
}

.chat__label--input:focus {
  outline: none;
  border-top: 1px solid var(--color-1);
}

.chat__label--btn {
  cursor: pointer;
  width: 20%;
  flex: 1 1;
  background: transparent;
  color: #eee;
  border: none;
  border-right: 2px solid var(--color-1);
  transition: all 0.2s;
}
.chat__label--btn:hover {
  border-right: 10px solid var(--color-1);
}
.chat__label--btn:focus {
  outline: none;
  border-right: 10px solid var(--color-1);
}

.chat__head {
  font-family: "Ubuntu", sans-serif;
  width: 100%;
  /* background: #fff000; */
  color: #fff;
  border-bottom: 3px solid var(--color-1);
  padding: 1rem;
  text-align: center;
  font-size: 3rem;
  position: relative;
}

.chat__head:after {
  content: "";
  position: absolute;
  top: 52%;
  left: 1%;
  transform: translateY(-50%);
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: #00ff00;
}

.chat__head:before {
  content: "online";
  position: absolute;
  top: 50%;
  left: 3%;
  transform: translateY(-50%);
  font-size: 1.5rem;
  color: #999;
}

a,
a:link,
a:hover,
a:active,
a:visited {
  text-decoration: none;
}

.chat__head--cross {
  position: absolute;
  right: 2%;
  top: 50%;
  transform: translateY(-50%);
  color: #ff000099;
  font-size: 2rem;
}
.chat__head--cross:hover {
  color: #ff0000;
}

.messages {
  width: 100%;
  flex: 1 1;
  padding: 0.8rem;
  font-family: "PT Sans", sans-serif;
  overflow-y: auto;
  overflow-x: hidden;
  display: flex;
}

.messageContainer {
  width: 60%;
  height: auto;
  padding: 1rem;
  margin-bottom: 1rem;
  border-radius: 1rem;
  font-size: 1.5rem;
  color: #fff;
  border-bottom: 1px solid var(--color-1);
  border-top: 1px solid var(--color-1);
}

.right {
  margin-left: auto;
}

.user {
  margin-bottom: 0.7rem;
  display: inline-block;
  font-size: 1.2rem;
  color: #bbc;
  border-bottom: 1px solid var(--color-1);
}

.join {
  width: 40rem;
  height: 30rem;
  /* background: #333; */
  display: flex;
  flex-direction: column;
  align-items: center;
}

.join__head {
  width: 100%;
  text-align: center;
  font-size: 2.8rem;
  padding: 1rem;
  font-family: sans-serif;
  color: #ddd;
  /* background: var(--color-1); */
  border-bottom: 2px solid var(--color-1);
}

.join__body {
  width: 100%;
  flex: 1 1;
  display: flex;
  flex-direction: column;
  padding: 1.5rem;
}

.join__body--label {
  width: 100%;
  flex: 1 1;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  font-size: 1.7rem;
  color: #ccc;
  font-family: sans-serif;
}
.join__body--label:first-child {
  margin-bottom: 1rem;
}
.join__body--input {
  font-size: 2rem;
  background: #111;
  height: 5rem;
  border: none;
  border-bottom: 1px solid var(--color-1);
  color: #ddd;
  transition: all 0.2s;
  margin-top: 0.5rem;
  padding: 0.5rem;
}
.join__body--input:focus {
  outline: none;
  border-bottom-width: 3px;
}

.join__btn {
  cursor: pointer;
  width: 100%;
  border: none;
  color: #fff;
  padding: 1rem;
  font-size: 2.7rem;
  font-weight: 100;
  background: var(--color-1);
  opacity: 0.6;
}
.join__btn:hover {
  opacity: 1;
}
a,
a:link,
a:visited,
a:hover,
a:active,
a:focus {
  width: 100%;
  text-decoration: none;
  outline: none;
}

:root {
  --color-1: #bd0000;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  font-size: 62.5%;
}

