.App {
  width: 100%;
  min-height: 100vh;
  background: #111;
  display: flex;
  justify-content: center;
  align-items: center;
}

::-webkit-scrollbar {
  width: 6px;
}

::-webkit-scrollbar-thumb {
  background: rgba(100, 100, 100, 0.1);
  border-radius: 1rem;
  border-top: 10px solid #bd000085;
  border-bottom: 10px solid #bd000085;
}
::-webkit-scrollbar-thumb:hover {
  background: rgba(80, 80, 80, 0.3);
  border-top: 10px solid #bd0000;
  border-top: 10px solid var(--color-1);
  border-bottom: 10px solid #bd0000;
  border-bottom: 10px solid var(--color-1);
}

